@import url(https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap);
.home{
    flex: 4 1;
}

.container{
    display: flex;
}
.sidebar {
    order: 1;
    flex: 1 1;
    width: 300px;
    height: 100vh;
    position: fixed;
    top: 50px;
    background: rgb(251, 251, 255);
    left: -80%;
    transition: 850ms;
    display: flex;
    -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}


.sidebar.active {
    display: block;
    left: 0;
    transition: 350ms;
    
}

.sidebar.is-active~.featuredItem {
    width: 100vw;
    height: calc(100vh - 28px);
    position: absolute;
    left: calc(220px + 28px);
}

.sidebarWrapper {
    padding: 20px;
    color: #555;
}

.sidebarMenu {
    margin-bottom: 10px;
}

.sidebarTitle {
    font-size: 13px;
    color: rgb(187, 186, 186);
}

.sidebarList {
    list-style: none;
    padding: 5px;
}

.sidebarListItem {
    padding: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
}

.sidebarListItem.active,
.sidebarListItem:hover {
    background-color: rgb(114, 214, 152);
}
.topbar {
    width: 100%;
    height: 50px;
    background: linear-gradient(90deg, rgba(2, 0, 36, 1) 0%, rgba(9, 121, 104, 1) 79%, rgba(88, 156, 228, 1) 100%);
    -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
    position: fixed;
    z-index: 950;
}

.topbarWrapper {
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo {
    font-weight: bold;
    font-size: 30px;
    color: #f4fefc;
    cursor: pointer;
}


.taxonomy {
    flex: 4 1;
    z-index: -1;
}

div.custom-container {
    background-color: white;
    display: flex;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    z-index: -1;
}

.node circle {
    fill: white;
    stroke: darkblue;
    stroke-width: 2px;
}

.node text {
    font-size: 11px;
    font-family: 'Source Sans 3',
        sans-serif;
    background-color: black;
    fill: black;
}

.node {
    cursor: pointer;
}

path.link {
    fill: none;
    stroke: lightgrey;
    stroke-width: 1.5px;
}

.featured {
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    margin-bottom: 20px;
    z-index: 0;
}

.featuredItem {
    flex: 1 1;
    width: inherit;
    height: 80%;
    overflow: scroll;
    margin: 0px 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
    z-index: 0;
}

.featuredTitle {
    font-size: 20px;
    z-index: 0;
}

.featuredDescriptionContainer {
    margin: 10px 0px;
    display: flex;
    align-items: center;
    z-index: 0;
}


.featuredSub {
    font-size: 15px;
    color: gray;
}

.safeguardsLink a.active,
.safeguardsLink a:hover {
    text-decoration: none;
    background-color: rgb(114, 214, 152);
}
html,
body {
  height: 100%;
}


.darkBG {
  background-color: rgba(0, 0, 0, 0.2);
  width: 100vw;
  height: 100rem;
  z-index: 0;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
  position: absolute;
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal {
  width: 84%;
  height: 84%;
  background: white;
  color: white;
  border-radius: 16px;
  overflow: scroll;
  opacity: 100%;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.04);
  z-index: 9999;
}

.modalHeader {
  height: 50px;
  background: white;
  overflow: hidden;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  z-index: 9999;
  opacity: 100%;
}

.heading {
  margin: 0;
  padding: 10px;
  color: #2c3e50;
  font-weight: 500;
  font-size: 18px;
  text-align: center;
}

.modalContent {
  padding: 10px;
  font-size: 14px;
  color: #2c3e50;
  text-align: left;
  margin: 10px;
  z-index: 9999;
  opacity: 100%;
}

.modalActions {
  position: absolute;
  bottom: 2px;
  margin-bottom: 10px;
  width: 100%;
}

.actionsContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.closeBtn {
  cursor: pointer;
  font-weight: 500;
  padding: 4px 8px;
  border-radius: 8px;
  border: none;
  font-size: 18px;
  color: #2c3e50;
  background: white;
  transition: all 0.25s ease;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.06);
  position: absolute;
  right: 0;
  top: 2%;
  left: 96%;
  align-self: flex-end;
  /* margin-top: -7px;
  margin-right: -7px; */
}

.closeBtn:hover {
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.04);
  transform: translate(-4px, 4px);
}

.deleteBtn {
  margin-top: 10px;
  cursor: pointer;
  font-weight: 500;
  padding: 11px 28px;
  border-radius: 12px;
  font-size: 0.8rem;
  border: none;
  color: #fff;
  background: #ff3e4e;
  transition: all 0.25s ease;
}

.deleteBtn:hover {
  box-shadow: 0 10px 20px -10px rgba(255, 62, 78, 0.6);
  transform: translateY(-5px);
  background: #ff3e4e;
}

.cancelBtn {
  margin-top: 10px;
  cursor: pointer;
  font-weight: 500;
  padding: 11px 28px;
  border-radius: 12px;
  font-size: 0.8rem;
  border: none;
  color: #2c3e50;
  background: #fcfcfc;
  transition: all 0.25s ease;
}

.cancelBtn:hover {
  box-shadow: none;
  transform: none;
  background: whitesmoke;
}
